* {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 20px;
}
body {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.container {
  top: 50%;
  left: 50%;
    position: absolute;
    transform: translate(-50%, -50%);
    margin: 0 auto;
    width: 256px;
    text-align: center;
    border: 5px solid #888;
    background: #d6d1d1;
    border-radius: 5px;
}

.keypad {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: minmax(60px, auto);
}

input[type='text'] {
  height: 75px;
  width: 249px;
  background-color: #080808;
  border: 0px;
  color: #fcf8f8;
  text-align: right;
  letter-spacing: 1px;
}

#clear {
  grid-column: 1/3;
  grid-row: 1;
  color: #f8f7f7;
}
#result {
  grid-column: 3/5;
  grid-row: 5;
  color: #faf8f8;
}

#backspace {
  color: #faf8f8;
}

button {
  margin: 5px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  background: #0a0a0a;
  color: #f8f7f7;
}

.highlight {
  background: #04b6fc;
}